	module.exports = {"data":{"markdownRemark":{"html":"<h1 id=\"installation\"><a href=\"#installation\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Installation</h1>\n<p>Gatsby Docs Kit was designed from the ground up to be easily installed and used to get your website up and running quickly. We have created a starter that contains example docs. To start:</p>\n<ol>\n<li>\n<p>Ensure you have the latest version of <a href=\"https://nodejs.org/en/download/\">Node</a> installed. We also recommend you install <a href=\"https://yarnpkg.com/en/docs/install\">Yarn</a> as well.</p>\n<blockquote>\n<p>While we recommend using latest Node version, your Node version must be at least 8.x.</p>\n</blockquote>\n</li>\n<li>\n<p>Ensure you have Git installed.</p>\n</li>\n<li>\n<p>Go into the root of your GitHub repo directory where you will be creating the docs.</p>\n</li>\n<li>\n<p>Run</p>\n<p><code class=\"language-text\">git clone &lt;TODO STARTER GIT&gt;</code></p>\n</li>\n<li>\n<p>Go to <code class=\"language-text\">docs/www</code> and run</p>\n<p><code class=\"language-text\">yarn install</code></p>\n</li>\n</ol>\n<h2 id=\"verifying-installation\"><a href=\"#verifying-installation\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Verifying Installation</h2>\n<p>Along with previously existing files and directories, your root directory will now contain a structure similar to:</p>\n<div class=\"gatsby-highlight\" data-language=\"bash\">\n      <pre class=\"language-bash\"><code class=\"language-bash\"><span class=\"token keyword\">.</span>\n├── docs\n│   ├── getting-started\n│   │   ├── creating-site.md\n│   │   ├── installation.md\n│   │   ├── introduction.md\n│   │   └── publishing.md\n│   ├── <span class=\"token punctuation\">..</span>.\n│   └── www\n│       ├── src\n│       │   ├── pages\n│       │   │   └── <span class=\"token punctuation\">..</span>.\n│       │   ├── layouts\n│       │   │   └── <span class=\"token punctuation\">..</span>.\n│       │   ├── templates\n│       │   │   └── <span class=\"token punctuation\">..</span>.\n│       │   └── globalReferences.js\n│       ├── gatsby-docs-kit.yml\n│       ├── gatsby-docs-kit-temp.json\n│       └── package.json</code></pre>\n      </div>\n<p>If you are still in <code class=\"language-text\">www</code> directory, then</p>\n<ol>\n<li>Run the local webserver via <code class=\"language-text\">yarn develop</code> or <code class=\"language-text\">npm develop</code>.</li>\n<li>Load the example site at <a href=\"http://localhost:8000\">http://localhost:8000</a>. You should see the example site loaded in your web browser.</li>\n<li>Visit <a href=\"http://localhost:8000/___graphql\">http://localhost:8000/___graphql</a> to explore your site's GraphiQL data and schema.</li>\n</ol>"}},"pathContext":{"relativePath":"GDK>../docs/getting-started/installation.md"}}

